@font-face {
  font-family: "whitney_light";
  src: url(/static/media/whitney_light.bdf8cddb.ttf);
}

body {
  margin: 0;
  padding: 0;
  font-family: "whitney_light", serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


#bg {
  transition: -webkit-filter 2s;
  transition: filter 2s;
  transition: filter 2s, -webkit-filter 2s;
  background-image: url(/static/media/newhomepageq.3ba81322.jpg);
  background-size: cover;
  z-index: -1;
  width: 100%;
  height: 100%;
  position: fixed;

}

.blurred {
  filter: blur(10px);
  -webkit-filter: blur(10px);
}

.clear {
  filter: blur(0px);
  -webkit-filter: blur(0px);
}

@media (hover: none) {
  #bg {
    background-image: url(/static/media/newhomemobile.f9bb8a47.jpg);
  }
}

.App {
  text-align: center;
}

.wrapper {
    width: 100%;
}

.navbar {
    display: -webkit-flex;
    display: flex;
    width: 60vw;
    box-sizing: border-box;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    border-top: 20px solid transparent;
}

.navbar-homepic {
    transition: -webkit-transform .8s ease-in-out;
    transition: transform .8s ease-in-out;
    transition: transform .8s ease-in-out, -webkit-transform .8s ease-in-out;
}

.navbar-homepic:hover {
    transform: rotate(180deg);
    -ms-transform: rotate(180deg);
    -webkit-transform: rotate(180deg);
}

.nav-items {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    width: 500px;
}

@media (hover: none) {
    .navbar {
        width: 100%;
        -webkit-flex-direction: column;
                flex-direction: column;
    }
    .nav-items {
        -webkit-flex-direction: column;
                flex-direction: column;
        height: 30%;
        width: auto;
        width: initial;
    }
}

#homepic {
    width: 30px;
    height: 30px;
}

.home {
    width: 200px;
}


.btn {
    width: auto;
    font-family: inherit;
    font-size: 30px;
    background: rgba(255, 255, 255, 0.6);
    /*filter: blur(20px);*/
    /*filter: blur(1px);*/
    color: black;
    border: black solid 5px;
    border-radius: 3px;
    box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
}

.btn:hover {
    background-color: antiquewhite;
    color: black;
    border-right: black solid 5px;
    border-bottom: black solid 5px;
    cursor: pointer;
}

.nav-visible {
    display: none;
}

@media (hover: none) {
    .nav-item {
        margin-bottom: 20px;
    }
    .pic-btn {
        display: inline-block;
    }
}

.pic-btn {
    background-color: transparent;
    background-color: initial;
    border: none;
}

.pic {
    width: 1052px;
    height: 744px;
}

